import { useState } from "react"
import { HL, Section } from "./Elements"
import { FP, H2, H5 } from "./TextStyles"
import { Button } from "./Buttons"
import { renderToString } from "react-dom/server"
import SvgMiniArrow from "./icons/MiniArrow"
import SvgMiniArrowRight from "./icons/MiniArrowRight"
import SvgMiniArrowDown from "./icons/MiniArrowDown"
import { useLayerContext, useModeContext } from "../context/brandLayers"


export const IconGroup=({array, name,click})=>{
    const [display,setDisplay]=useState(false)
    const toggleDisplay=()=>setDisplay(!display)
    return(
        <Section>
            <Section horizontal AICenter>
                <Button onClick={toggleDisplay}>{!display?<SvgMiniArrowRight/>:<SvgMiniArrowDown/>}</Button><H2 hug style={{wordWrap:true}}>{name} </H2><H5 hug style={{textAlign:'right'}}>({array.length})</H5>
            </Section>
            {display && <Section horizontal wrap>
                {array.map((icon)=>{
                    const onClick =()=>{
                        console.log(renderToString(icon.element))
                        click(icon)
                    }
                    return(
                        <Section onClick={onClick} hug style={{flex:1, minWidth:'60px',maxWidth:'60px'}}>
                            {icon.element}
                            <FP fit style={{textAlign:'center'}}>{icon.name}</FP>
                        </Section>
                    )
                })}
            </Section>}
            <HL/>

        </Section>
    )
}


